.modal-wrapper {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	animation: show .2s;
	transform: scale(1);
	}
	@keyframes show {
	  from{
		transform: scale(0);
	  } to {
		transform: scale(1);
	    
	  }
	}

.modal-backdrop {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 100;	
	background-color: rgba(0,0,0,0.3)
}

.modal-content {
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	min-height: 30%;
	width: 50%;
	background-color: #292929;
  	box-shadow: 3px 3px 8px black;
  	z-index: 500;
  	overflow-y: auto;
  	padding: 40px;
}