    .card-columns {
        -webkit-column-count: 1;
                column-count: 1;
    }
}

@media (min-width: 768px) {
    .card-columns {
        -webkit-column-count: 2;
                column-count: 2;
    }
}

@media (min-width: 992px) {
    .card-columns {
        -webkit-column-count: 3;
                column-count: 3;
    }
}

@media (min-width: 1200px) {
    .card-columns {
        -webkit-column-count: 3;
                column-count: 3;
    }
}

.poll-form input[type=text], input[type=password] {
  padding: 10px;
  width: 100%;
  outline: none;
  background: none;
  color: #e1e1e1;
  border: 2px solid grey;
  border-radius: 24px;
  padding-left: 15px;
  transition: 0.25s
}

.poll-form input[type="text"]:focus,
.poll-form input[type="password"]:focus {
  border-color: #943dff
}

.poll-form input[name=choice] {
  width: 75%;
}

.rounded-border  {
  border: 2px solid grey;
  border-radius: 10px;
}

.comment-form textarea {
  color: #e1e1e1;
  width: 100%;
  height: 100px;
  padding: .5rem;
  outline: none;
  background: none;
  transition: 0.5s ease all;
  
}

.comment-form textarea:focus {
  border-color: #943dff;
}

.comment-form textarea:active {
  transform: scale(0.95);
}

.form-container {
    width: 100%;
    padding: 40px;
    background: #292929;
    border: 0px;
    border-radius: 15px;
    box-shadow: 3px 3px 8px black;
    position: absolute;
    top: 50%;
    text-align: center;
    transition: 0.25s;
    margin-top: 40px

}

.form-container input[type="text"],
.form-container input[type="password"] {
    border: 0;
    background: none;
    display: block;
    margin: 15px auto;
    text-align: center;
    border: 2px solid grey;
    padding: 10px 10px;
    width: 80%;
    outline: none;
    color: #e1e1e1;
    border-radius: 24px;
    transition: 0.25s
}

.form-container input[type="text"]:focus,
.form-container input[type="password"]:focus {
    width: 90%;
    border-color: #943dff
}

input[type="file"] {
  display: none;
}

  

.custom-btn {
  border: 0;
  background: none;
  display: block;
  text-align: center;
  padding: 14px 40px;
  outline: none;
  border-radius: 24px;
  transition: 0.25s;
  cursor: pointer
}

.purple-btn {
  color: #943dff;
  border: 2px solid #943dff;
}
.purple-btn:hover {
  background: #943dff;
  transition: all 0.3s ease;
}
.red-btn {
  color: red;
  border: 2px solid red;
}

.red-btn:hover {
  background: red;
  transition: all 0.3s ease;
}

.hover {
  transition: all 0.25s ease;
}

.hover:hover {
  cursor: pointer;
  transform: scale(1.2)
}

.active {
  transition: all 0.25s ease;
}

.active:active {
  transform: scale(0.95)
}


.choice-btn {
  color: #943dff;
  height: 4em;
  width: 100%;
  border: 5px solid linear-gradient(120deg, rgba(148,61,255,1) 0%, rgba(157,67,168,1) 100%);
  border-radius: 10px;
  position: relative;
  margin-bottom: 10px;
  transition: all 0.2s ease-in-out;
}

.choice-btn:hover {
  cursor: pointer;
}

.choice-btn-hover:hover {
  background: #943dff;
  cursor: pointer;
  width: 95%;
} 

.choice-btn:active {
  transform: scale(0.95);
}


.choice-btn::before { 
  content: attr(data-label);
  color:  #e1e1e1;
  font-size: 14px;
  position: absolute;
  top: 21px;
  left: 22px;
}


.bar {
  background: linear-gradient(120deg, rgba(148,61,255,1) 0%, rgba(157,67,168,1) 100%);
  display: block;
  border-radius: 10px;
  height: 100%;
  width: 0%;
  transition: width 1s ease-in-out;
}

.percent {
  font-size: 16px;
  color: #e1e1e1;
  position: absolute;
  top: 17px;
  right: 20px;
}



[tooltip-text]:before {
	content: attr(tooltip-text);
	position: absolute;
	opacity: 0;
	z-index: 2;

	/* customizable */
	transition-delay: all 1s ease;
	padding: 5px 10px;
	color: #e1e1e1;
	border-radius: 10px;
	font-size: 11px;
}

[tooltip-text]:hover:before {
    /* needed - do not touch */
    opacity: 1;
    
    /* customizable */
    background: #1a1a1a;
    margin-top: -20px;
    margin-left: 20px;    
}

.modal-wrapper {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	-webkit-animation: show .2s;

	        animation: show .2s;
	transform: scale(1);
	}
	@-webkit-keyframes show {
	  from{
		transform: scale(0);
	  } to {
		transform: scale(1);
	    
	  }
	}
	@keyframes show {
	  from{
		transform: scale(0);
	  } to {
		transform: scale(1);
	    
	  }
	}

.modal-backdrop {
	position: fixed;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	z-index: 100;	
	background-color: rgba(0,0,0,0.3)
}

.modal-content {
	position: relative;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	min-height: 30%;
	width: 50%;
	background-color: #292929;
  	box-shadow: 3px 3px 8px black;
  	z-index: 500;
  	overflow-y: auto;
  	padding: 40px;
}
